<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen>
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
            <el-row :gutter="15">
                <el-col :span="8">
                    <el-form-item label="工商执照注册号" prop="registerNo">
                        <el-input v-model="dialogForm.registerNo" placeholder="请填写工商执照注册号" maxlength="18"></el-input>
                    </el-form-item>
                    <el-form-item label="所在地" prop="companyPlace">
                        <el-select placeholder="请选择所在地" clearable v-model="dialogForm.companyPlace">
                            <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>                            
                        </el-select>
                    </el-form-item>  
                    <el-form-item label="企业负责人姓名" prop="leaderName">
                        <el-input v-model="dialogForm.leaderName" placeholder="请填写企业负责人姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="经营范围" prop="businessScope">
                        <el-select placeholder="请选择经营范围" multiple clearable v-model="dialogForm.businessScope">
                            <el-option v-for="(item, index) in dic['经营范围']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="从业人员数量" prop="employeeCount">
                        <el-input v-model.number="dialogForm.employeeCount" placeholder="请填写从业人员数量"></el-input>
                    </el-form-item>
                    <el-form-item label="是否落实100%先验视后封箱" prop="isBeginCheck" label-width="200px">
                        <el-radio-group v-model="dialogForm.isBeginCheck">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <techo-position ref="position" :code="dialogForm.districtCode" :placement="'right'" @formLonLat="getLonlat"></techo-position>
                    <div class="lon-lat">
                        <span>经度：</span>
                        <span>{{ dialogForm.x }}</span>
                        <span>经度：</span>
                        <span>{{ dialogForm.y }}</span>
                    </div>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="寄递物流企业名称" prop="companyName" label-width="180px">
                        <el-input v-model="dialogForm.companyName" placeholder="请填写寄递物流企业名称"></el-input>
                    </el-form-item>
                    <el-form-item label="企业详址" prop="companyPlaceDetail" label-width="180px">
                        <el-input v-model="dialogForm.companyPlaceDetail" placeholder="请填写企业详址"></el-input>
                    </el-form-item>
                    <el-form-item label="企业负责人联系方式" prop="leaderContact" label-width="180px">
                        <el-input v-model="dialogForm.leaderContact" placeholder="请填写企业负责人联系方式"></el-input>
                    </el-form-item>
                    <el-form-item label="企业类型" prop="companyType" label-width="180px">
                        <el-select placeholder="请选择企业类型" clearable v-model="dialogForm.companyType">
                            <el-option v-for="(item, index) in dic['企业类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="监控摄像机数量" prop="monitoringCount" label-width="180px">
                        <el-input v-model.number="dialogForm.monitoringCount" placeholder="请填写监控摄像机数量"></el-input>
                    </el-form-item>
                    <el-form-item label="是否落实100%寄递实名制" prop="isRealName" label-width="180px">
                        <el-radio-group v-model="dialogForm.isRealName">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="登记注册类型" prop="registType">
                        <el-select placeholder="请选择登记注册类型" clearable v-model="dialogForm.registType">
                            <el-option v-for="(item, index) in dic['登记注册类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="企业联系方式" prop="companyContact">
                        <el-input v-model="dialogForm.companyContact" placeholder="请填写企业联系方式"></el-input>
                    </el-form-item>
                    <el-form-item label="控股情况" prop="bviInfo">
                        <el-select placeholder="请选择控股情况" clearable v-model="dialogForm.bviInfo">
                            <el-option v-for="(item, index) in dic['控股情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="服务品牌" prop="serviceBrand">
                        <el-input v-model="dialogForm.serviceBrand" placeholder="请填写服务品牌"></el-input>
                    </el-form-item>
                    <el-form-item label="X光机数量" prop="xrayCount">
                        <el-input v-model.number="dialogForm.xrayCount" placeholder="请填写X光机数量"></el-input>
                    </el-form-item>
                    <el-form-item label="是否落实100%X光机安检" prop="isCheckXray" label-width="180px">
                        <el-radio-group v-model="dialogForm.isCheckXray">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/district_check_service.js";

export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dic: {
        登记注册类型: [],
        控股情况: [],
        经营范围: [],
        企业类型: [],
        行政区划: []
      },
      disCodeOption: [],
      dialogForm: {
        x: "",
        y: "",
        businessScope: [],
        isBeginCheck: 0,
        isRealName: 0,
        isCheckXray: 0,
        districtCode:[]
      },
      teamRules: {
        registerNo: [
          { required: true, message: "请输入工商执照注册号", trigger: "blur" },
          { min: 18, max: 18, message: "长度为18位", trigger: "blur" }
        ],
        companyName: [
          {
            required: true,
            message: "请输入寄递物流企业名称",
            trigger: "blur"
          },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],
        companyPlace: [
          { message: "请选择企业所在地", trigger: "change" }
        ],
        companyPlaceDetail: [
          {message: "请输入企业详址", trigger: "blur" },
          { min: 2, max: 30, message: "长度在 2 到 30 个字符", trigger: "blur" }
        ],
        companyContact: [
          {message: "请输入企业联系方式", trigger: "blur" },
          util.validate.tellAndPhone()
        ],
        leaderName: [
          { required: true, message: "请输入企业负责人姓名", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符", trigger: "blur" }
        ],
        leaderContact: [
          {required: true, message: "请输入企业负责人联系方式",trigger: "blur"},
          util.validate.tellAndPhone()
        ],
        registType: [
          {message: "请选择登记注册类型", trigger: "change" }
        ],
        bviInfo: [
          {message: "请选择控股情况", trigger: "change" }
        ],
        businessScope: [
          { required: true, message: "请选择经营范围", trigger: "change" }
        ],
        companyType: [
          { required: true, message: "请选择企业类型", trigger: "change" }
        ],
        serviceBrand: [
          {message: "请输入服务品牌", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符", trigger: "blur" }
        ],
        employeeCount: [
          { type:"number",message: "请输入从业人员数量", trigger: "blur" },
        ],
        monitoringCount: [
          {type:"number", message: "请输入监控摄像机数量", trigger: "blur" },
        ],
        xrayCount: [
          {type:"number", message: "请输入X光机数量", trigger: "blur" },
        ],
      }
    };
  },
  watch: {
    isAdd(value) {
      if (value == false) {
          this.$nextTick(() => {
              this.$refs["position"].addMarker({
                lng: this.dialogForm.x,
                lat: this.dialogForm.y
              });
          })
      }
    }
    },
  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  
  methods: {
    getLonlat(value) {
      this.dialogForm.x = value.lng;
      this.dialogForm.y = value.lat;
    },
    /** 保存 */
    submitForm() {
      if (this.dialogForm.x == undefined || this.dialogForm.x == "") {
        this.$popup.showNotification({
          type: "warning",
          message: "请选择地理位置！"
        });
      } else {
        this.$refs["dialogForm"].validate(valid => {
          if (valid) {
            let param = _.cloneDeep(this.dialogForm);
            // 经营范围 后台只支持单选
            param.businessScope = _.first(param.businessScope);
            param.dicMap && delete param.dicMap;
            if (this.isAdd == true) {
              // 添加 物流安全管理
              service.requestSaveLogisticsSafe(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "物流安全管理添加成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            } else {
              service.requestUpdateLogisticsSafe(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "物流安全管理修改成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {
        x: "",
        y: "",
        businessScope: [],
        isBeginCheck: 0,
        isRealName: 0,
        isCheckXray: 0
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>