<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :recordCount="recordCount" :disCodeOption="disCodeOption" @onAdd="handleAdd"
            @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit" @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">
                    <el-col :span="10">
                        <span class="techo-list__header-col-txt-5">物流企业名称：</span>
                        <div class="techo-list__header-col-left-5">
                        <el-input placeholder="请输入姓名" :maxlength="10" size="small" class="techo-list__header-select-full"
                            clearable v-model="queryForm.name"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="9">
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'"></techo-details>
        <vAddDialog ref="vAddDialog"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/district_check_service.js";
import vAddDialog from "./logistics-safe-add";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "物流安全管理",
      disCodeOption: [],
      queryForm: {
        districtCode: "",
        name: ""
      },
      header: {
        工商执照注册号: "registerNo",
        寄递物流企业名称: "companyName",
        //所在地: "companyPlace",
        //企业详址: "companyPlaceDetail",
        //企业联系方式: "companyContact",
        企业负责人姓名: "leaderName",
        企业负责人联系方式: "leaderContact",
        //登记注册类型: "dicMap.registType",
        //控股情况: "dicMap.bviInfo",
        经营范围: "dicMap.businessScope",
        企业类型: "dicMap.companyType"
        //服务品牌: "serviceBrand",
        //从业人员数量: "employeeCount",
        //监控摄像机数量: "monitoringCount",
        //X光机数量: "xrayCount",
        //"是否落实100%先验视后封箱": "dicMap.isBeginCheck",
        //"是否落实100%寄递实名制": "dicMap.isRealName",
        //"是否落实100%X光机安检": "dicMap.isCheckXray",
        //网格编码: "districtCode"
      },
      data: [],
      recordCount: 0,
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "logisticsSafe"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加物流安全管理 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].dialogForm.districtCode = this.queryForm.districtCode;
    },
    /** 删除物流安全管理 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteLogisticsSafe(item.id, data => {
            count == tableSelection.length - 1 && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "物流安全管理删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "物流安全管理删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      let data = _.cloneDeep(row);
      data.businessScope =
        (data.businessScope && data.businessScope.split(",")) || [];
      this.$refs["vAddDialog"].dialogForm = data;
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageLogisticsSafe(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageLogisticsSafe(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              if (item.dicMap) {
                item.dicMap.isBeginCheck = item.isBeginCheck == 1 ? "是" : "否";
                item.dicMap.isRealName = item.isRealName == 1 ? "是" : "否";
                item.dicMap.isCheckXray = item.isCheckXray == 1 ? "是" : "否";
              }
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            if (item.dicMap) {
              item.dicMap.isBeginCheck = item.isBeginCheck == 1 ? "是" : "否";
              item.dicMap.isRealName = item.isRealName == 1 ? "是" : "否";
              item.dicMap.isCheckXray = item.isCheckXray == 1 ? "是" : "否";
            }
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>
